function time(step) {
	var i =0;
	
	var cards = $('.cards ul li');
	$(".popover.time .overscroll").on('scroll', function () {
		var st = $(this).scrollTop();
		divs.css({
			'opacity': (1 - st / 25)
		});
	});

	$(".cards ul li:last-of-type").on('click', function () {
		$("body").css("pointer-events", "none");
		$(".popover.time .overscroll").animate({
			scrollTop: $(".cards")[0].scrollHeight
		}, 500);
	});

	var card = $(".cards ul li").height() - 65;
	$(".cards ul li").css("margin-top", -card);
	$(".cards ul li:last-child").css("margin-bottom", -card);
	$('.cards ul li').on("click", function () {
		$(this).toggleClass("current");
		$(this).next().toggleClass("push");
	});

	$('.cards ul li').each(function () {
		$(this).css({
			"z-index": i
		});
		$(this).attr('data-info', 1);
		i++;
	});

	$(".popover.time").addClass("show");
	setTimeout(function () {
		$(".popover.time .overscroll").css("opacity", "1");
	}, 2000);

	var daysAroundDate=4;
	if ($(".popover.time").hasClass("return")) {
		var selectedDepartureDate=new Date(saveTheDate.returning);
	} else {
		var selectedDepartureDate=new Date(saveTheDate.leaving);
	}

	$(".popover.time .datepicker").html("");
	for (i=(0-daysAroundDate);i<daysAroundDate+1;i++) {
		thisDate = addDays(selectedDepartureDate,i);
		var dLi = document.createElement("LI");
		if (i==0) {
			dLi.className="highlight";
		}
		dLi.innerHTML='<div class="date" data-date="'+thisDate.getTime()+'"><span class="datedate">'+weekday[thisDate.getDay()]+' '+thisDate.getDate()+' ' + shortmonth[thisDate.getMonth()]+'</span><br/><span class="price" data-price="'+((35+priceDiff[i+daysAroundDate])*numberTraveller)+'">'+currency+((35+priceDiff[i+daysAroundDate])*numberTraveller)+'</span></div>';
		$(".popover.time .datepicker").append(dLi);
	}
	$(".popover.time .datepicker").scrollLeft(0);
	var targetScrollPos=$(".popover.time .datepicker .highlight").offset().left-$(window).width()/4;
	$(".popover.time .datepicker").scrollLeft(targetScrollPos);

	$(".popover.time button").bind("click",function() {
		selectTicket(this);
	})
}
function selectTicket(obj) {
	$(".popover.time .overscroll").animate({ scrollTop: "0px" },300);
	if (!$(".popover.time").hasClass("return") && !$(".popover.time").hasClass(".final") ) {
		$(".popover.time").addClass("return").removeClass("final");;
		console.log("added class return");
		$(".popover.time .header p").html("Return ticket");
		loadReturnFile();
		$(".out_bar .departure").html($(obj).parent().find(".deptime").data("departure"));
		$(".out_bar .arrival").html($(obj).parent().find(".deptime").data("arrival"));
		$(".out_bar time").html($(".datepicker .highlight .datedate").html().substring(4,100));
		$(".out_bar .price").html($(obj).parent().find(".deptime .price").html());
	   	return false;
	} else if ($(".popover.time").hasClass("return") && !$(".popover.time").hasClass(".final") ) {
		$(".return_bar time").html($(".datepicker .highlight .datedate").html().substring(4,100));
		$(".return_bar .departure").html($(obj).parent().find(".deptime").data("departure"));
		$(".return_bar .arrival").html($(obj).parent().find(".deptime").data("arrival"));
		$(".return_bar .price").html($(obj).parent().find(".deptime .price").html());
		$(".total .price").html(currency+(parseFloat($(".out_bar .price").html().replace(currency,""))+parseFloat($(".return_bar .price").html().replace(currency,""))));
		$(".popover.time .header p").html("Summary");
		$(".popover.time").addClass("final").removeClass("return");
		$(".popover.time").removeClass("filtered");
		$("#slider-range").slider('values',0,0);
		$(".range input#start").val("00:00")
		$("#slider-range").slider('values',1,24);
		$(".range input#end").val("24:00")
	}
   
}

$(".datepicker").scroll(function () {
	
	$(".cards").css("opacity", "0");
	$(".cards ul li").removeClass("current push");
	$(".datepicker li").each(function () {
		if (isElementInViewport(this)) {
			$(this).addClass("highlight");
			$(this).next().addClass("next");
	 	$(this).prev().addClass("previous");
		} else {
			$(this).removeClass("highlight");
			$(this).next().removeClass("next");
			$(this).prev().removeClass("previous");
		}
	})
	clearTimeout($.data(this, "scrollCheck"));
	$.data(this, "scrollCheck", setTimeout(function () {
		$(".cards").css("opacity", "1");
		var priceDiff = (parseFloat($(".popover.time .datepicker li.highlight .price").data("price"))-35);
		console.log("price difference",priceDiff);

		$(".cards li").each(function() {
			var origPrice = parseFloat($(this).find(".price").data("price"));
			$(this).find(".price").html(currency+((origPrice+priceDiff))).data("buyprice",(origPrice+priceDiff));
			var duration = parseFloat($(this).find(".deptime").data("duration"))*60000;
			var arrivalDate = new Date($(".popover.time .datepicker li.highlight .date").data("date")+duration);
			$(this).find(".arrinfo").html("Arrives "+weekday[arrivalDate.getDay()]+" " + arrivalDate.getDate()+" " + shortmonth[arrivalDate.getMonth()]);
		})
	}, 250));
});		
