$(".menu").bind("touchend",function() {
	if ($(".popover.dest input[type=search]").is(":focus")) {
		$(".popover.dest input[type=search]").blur();
	}
})
$(".menu").scroll(function () {
	$(".results").css({
		"opacity": "0"
		, "pointer-events": "none"
	});
	$(".menu li").each(function () {
		if (isElementInViewport(this)) {
			$(this).addClass("highlight");
			$(this).next().addClass("next");
			$(this).prev().addClass("previous");

			$(".results:nth-of-type(" + $(this).index() + ")").css({
				"opacity": "1"
				, "pointer-events": "all"
			});
		} else {
			$(this).removeClass("highlight");
			$(this).next().removeClass("next");
			$(this).prev().removeClass("previous");
		}
	})
	clearTimeout($.data(this, "scrollCheck"));
	$.data(this, "scrollCheck", setTimeout(function () {
		$(".sfgd").css("opacity", "1");
	}, 250));
});

function isElementInViewport(el) {
	if (typeof jQuery === "function" && el instanceof jQuery) {
		el = el[0];
	}
	var rect = el.getBoundingClientRect();
	return (
		rect.left >= 0 &&
		rect.right <= (window.innerWidth || document.documentElement.clientWidth) /*or $(window).width() */
	);
}
var navigation = function() {
	var scrollTop = 0;

	function scrollHandler(e) {
		var prevScroll	  = scrollTop;
		scrollTop		   = $(window).scrollTop();
		var windowHeight	= $(window).innerHeight();
		var scrolling_down  = (prevScroll<scrollTop);

		// show/hide nav depending on scroll direction and scroll 
		if (scrollTop>$(".navbar").height() && scrolling_down) {
		   $(".navbar").addClass("hidden");
		}  else {
			$(".navbar").removeClass("hidden");
		}

	   
	}
	return {
		Init: function() {
			$(window).bind("scroll",scrollHandler);
		}
	}
}();
navigation.Init();